.publication {
    font-size: $type-size-6;
}

.publication-content {}

.publication__type-title {
    font-size: $type-size-3;
    font-weight: bold;
    color: mix(#fff, $gray, 25%);

    padding-top: 2.0em;
    //padding-bottom: 0.7em;
    //-bottom: 1px solid $border-color;

    hr {
        margin-top: 0.1em;
        margin-bottom: 0.5em;
    }
}

.publication__item {}

.publication__item-heading {
    .title {
        font-size: $type-size-4-5;
        font-weight: bold;
    }
}

.publication__item-event {
    font-style: italic;
    a {
        color: $text-color;
        &:hover {
            color: $link-color;
        }
    }
}

.publication__item-authors {
    a {
        //text-decoration: underline;
        //color: mix(#fff, $text-color, 15%);
        color: $text-color;

        &:hover {
            color: $link-color;
        }
    }
}

.publication__item-content {}

.publication__item-awards {
    //color: mix(#fff, $text-color, 15%);
    color: mix(#fff, $text-color, 50%);

    a {
        //text-decoration: underline;
        color: mix(#fff, $text-color, 15%);

        &:hover {
            color: $link-color;
        }
    }

    .link-title {
        //background-color: mix(#fff, $link-color, 75%);
        display: inline-block;
    }
}

.publication__item-transitions {
    //color: mix(#fff, $text-color, 15%);
    color: mix(#fff, $text-color, 50%);

    a {
        //text-decoration: underline;
        color: mix(#fff, $text-color, 15%);

        &:hover {
            color: $link-color;
        }
    }

    .link-title {
        //background-color: mix(#fff, $link-color, 75%);
        display: inline-block;
    }
}

.publication__item-mentions {
    color: mix(#fff, $text-color, 50%);

    a {
        //text-decoration: underline;
        color: mix(#fff, $text-color, 15%);

        &:hover {
            color: $link-color;
        }
    }

    ul,li {
        list-style-type: none;
        margin-top: 0.0em;
        margin-bottom: 0.0em;
        padding-left: 0.0em;
    }

    .link-title {
        font-style: italic;
    }
}

.publication__item-sep {
    hr {
        margin-top: 0.5em;
        margin-bottom: 0.5em;
    }
}
